const config = {
    env: 'local',
    get apiIpWithPort() {
        return this.env === 'prod' ? '13.232.89.174:5500' : 'localhost:5500'
    },
    get apiBaseurl() {
        return 'http://' + this.apiIpWithPort
    },
    get apiUrl() {
        return this.apiBaseurl + '/admin/';
    }

}

export default config;