import { Button, notification, Space } from 'antd';
import React from 'react';
import employeeService from '../services/employee';
import { AntdMsg } from '../utils/Antd';
import commonObj from '../commonObj';

export function meetingRequest(data) {
    const key = data.extra._id;
    const rejectRequest = () => {
        employeeService.rejectRequestedMeeting({ meetingid: key }).then(res => {
            AntdMsg(res.message);
            notification.close(key);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }
    const startRequest = () => {
        employeeService.startRequestedMeeting({ meetingid: key }).then(res => {
            AntdMsg(res.message);
            notification.close(key);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }
    const btn = (
        <Space>
            <Button type="primary" size="small" onClick={startRequest}>
                Accept
            </Button>
            <Button type="primary" size="small" onClick={rejectRequest} danger>
                Reject
            </Button>
        </Space>
    );
    notification.open({
        message: data.message,
        description: data.description,
        btn,
        key,
        duration: 0,
        closeIcon: <></>
    });
};

export function meetingRequestRejected(data) {
    const key = data.extra._id;
    notification.open({
        message: data.message,
        description: data.description,
        key,
        duration: 0,
        type: 'error'
    });

    if(commonObj.employeeCheckMeeting){ commonObj.employeeCheckMeeting(); }
};


export function meetingRequestCanceled(data) {
    const key = data.extra._id;
    notification.close(key);
    notification.open({
        message: data.message,
        description: data.description,
        key,
        duration: 0,
        type: 'error'
    });
};

export function meetingRequestStarted(data) {
    const key = data.extra._id;
    notification.open({
        message: data.message,
        description: data.description,
        key,
        type: 'success'
    });
    if(commonObj.employeeCheckMeeting){ commonObj.employeeCheckMeeting(); }
};