import axios from "../utils/axios";

export default class service {

    static baseURL = '';

    static listProjectModules(data) {
        return axios.get(this.baseURL + '/master/project-modules', { params: data });
    }
    static listEmployees(data) {
        return axios.get(this.baseURL + '/master/employees-list', { params: data });
    }
    static makePunchedin(data) {
        return axios.post(this.baseURL + '/master/make-punchin', data);
    }
    static makePunchedout(data) {
        return axios.post(this.baseURL + '/master/make-punchout', data);
    }
    static checkPunchedin() {
        return axios.get(this.baseURL + '/master/check-punchedin');
    }

    static startBreak(data) {
        return axios.post(this.baseURL + '/master/start-break', data);
    }
    static stopBreak(data) {
        return axios.post(this.baseURL + '/master/stop-break', data);
    }

    static requestMeeting(data) {
        return axios.post(this.baseURL + '/meeting/request', data);
    }
    static rejectRequestedMeeting(data) {
        return axios.post(this.baseURL + '/meeting/reject', data);
    }
    static startRequestedMeeting(data) {
        return axios.post(this.baseURL + '/meeting/start', data);
    }
    static endMeeting(data) {
        return axios.post(this.baseURL + '/meeting/end', data);
    }
    static checkMeeting(data) {
        return axios.get(this.baseURL + '/meeting/check-meeting', {params: data});
    }
    static cancelRequestedMeeting(data) {
        return axios.post(this.baseURL + '/meeting/cancel-request', data);
    }
    static profile(data){
        return axios.get("master/profile", {params: data});
    }
    static updatePassword(data) {
        return axios.post(this.baseURL + '/master/update-password', data);
    }
}